import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { API_URL } from '../app.constants';

@Injectable({
  providedIn: 'root'
})
export class BackendService {

  constructor(private http: HttpClient) { }

  getOrders(urlEndpoint: string) {
    return this.http.get<Order>(urlEndpoint);
  }

  postOrder(order: object) {
    return this.http.post(`${API_URL}/orders`, order);
  }

  deleteOrder(order: object) {
    const payload = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: order
    };
    return this.http.delete(`${API_URL}/orders`, payload);
  }

  updateOrder(order: object) {
    return this.http.put(`${API_URL}/orders`, order);
  }
}

export interface Order {
  id: number;
  orderName: string;
  price: number;
  discounted: boolean;
  clerk: object;
  totalBill: number;
}
