import { Component, OnInit } from '@angular/core';
import { BackendService, Order } from '../service/backend.service';
import { API_URL } from '../app.constants';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  showUpdateBtn = false;

  totalRegularBill: number;
  totalDiscountedBill: number;
  orderArr: object[];

  // Order properties
  orderName: string;
  orderPrice: number;
  isOrderDiscounted: boolean;
  orderId: number;
  orderObj: object;

  newOrderName: string;
  newOrderPrice: number;
  newOrderDiscounted: boolean;

  clerk: object;

  message: string;
  messageStyle: string;

  constructor(private backend: BackendService) { }

  ngOnInit() {
    this.showRegularBill();
    this.showDiscountedBill();
  }

  showRegularBill() {
    this.backend.getOrders(`${API_URL}/order/bill/regular`).subscribe(
      (data: Order) => {
        this.clerk = data['clerk'];
        this.orderArr = data['orderList'];
        this.totalRegularBill = data['totalBill'];
      }, () => {
        this.message = 'Cannot load details. Something went wrong.'
        this.messageStyle = 'alert';
      }
    );
  }

  showDiscountedBill() {
    this.backend.getOrders(`${API_URL}/order/bill/discounted`).subscribe(
      (data: Order) => {
        this.totalDiscountedBill = data['totalBill'];
      }, () => {
        this.message = 'Cannot load details. Something went wrong.';
        this.messageStyle = 'alert';
      }
    );
  }

  addOrder() {
    // create new order obj
    const orderObj = {
      orderName: this.newOrderName,
      price: this. newOrderPrice,
      discounted: this.newOrderDiscounted
    };

    this.backend.postOrder(orderObj).subscribe(() => {
      this.showRegularBill();
      this.showDiscountedBill();
      this.setEmptyFields();
      this.message = 'Order is successfully added.';
      this.messageStyle = 'success';
    }, () => {
        this.message = 'Unable to add order. Something went wrong.';
        this.messageStyle = 'alert';
    });
  }

  editItem(order: Order) {
    this.orderId = order.id;
    this.orderName = order.orderName;
    this.orderPrice = order.price;
    this.isOrderDiscounted = order.discounted;
    this.showUpdateBtn = true;
  }

  createOrderObj() {
    this.orderObj = {
      id: this.orderId,
      orderName: this.orderName,
      price: this.orderPrice,
      discounted: this.isOrderDiscounted
    };
  }

  updateOrder() {
    this.createOrderObj();
    this.backend.updateOrder(this.orderObj).subscribe(() => {
      this.showRegularBill();
      this.showDiscountedBill();
      // this.showUpdateBtn = false;
      this.cancelUpdate();
      this.message = 'Order is successfully updated.';
      this.messageStyle = 'success';
    }, () => {
      this.message = 'Unable to update order. Something went wrong.';
      this.messageStyle = 'alert';
    });
  }

  cancelUpdate() {
      this.showUpdateBtn = false;
      this.orderName = '';
      this.orderPrice = 0;
      this.isOrderDiscounted = false;
  }

  setEmptyFields() {
      this.newOrderName = '';
      this.newOrderPrice = 0.0;
      this.newOrderDiscounted = false;
  }

  deleteItem(order: Order) {
    this.backend.deleteOrder(order).subscribe(() => {
      this.showRegularBill();
      this.showDiscountedBill();
      this.message = 'Order is successfully deleted.';
      this.messageStyle = 'success';
    }, () => {
      this.message = 'Unable to delete order. Something went wrong.';
      this.messageStyle = 'alert';
    });
  }

  closeBtn() {
    this.message = '';
  }
}
